<template lang="pug">
    div(class="page")
        Header
        section(class="service-details")
            div(class="container")
                div(class="subtitle")
                    fa(class="theicon" :icon="['fas', 'mobile-alt']")
                    h2 {{ service.title }}
                div(class="service-content")
                    .row 
                        .col-lg-7
                            .service-text(v-html="service.body")
                        .col-lg-5
                            .service-image
                                img(:src="service.single_image", alt="")
        Footer
</template>

<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";

import Header from "@/components/global/Header.vue";
import Footer from "@/components/global/Footer.vue";

Vue.use(VueAxios, axios);

export default {
    name: "SingleService",
    components: {
        Header,
        Footer,
    },
    data() {
        return {
            url: this.$route.params.url,
            service: [],
        };
    },
    mounted() {
        this.axios.get("services/" + this.url).then((singleServiceApi) => {
            this.service = singleServiceApi.data.data.services[0];
        });
    },
};
</script>
